.card {
    width: 100%;
    overflow: hidden;
    padding: 25px;
}

.card .nav {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.card .img {
    width: 100%;
    height: 270px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    animation:rotate 20s linear infinite;
    animation-play-state: paused;
    user-select: none;
}
@keyframes rotate {
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}
.card canvas{
    position: absolute;
    width: 100%;
    height:100%;
    left: 0;
    bottom: 0;
    
}

.card .img.playing{
    animation-play-state:running;
}

.card .img img {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 50%;
}


.card .details {
    width: 100%;
    text-align: center;
}

.card .details .title {
    font-size: 1.2rem;
}

.card .details .artist {
    color: #bbb;
}


/* progresss */
.card .progress{
    margin-top:15px;
}
.card .progress input{
    width:100%;
    height: 4px;
}

.card .timer{
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: .8rem;
    color:#bbb;
}

.card .controls{
    width:100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top:20px;
    margin-bottom:30px;
}
.card .controls #prev,
.card .controls #next{
   font-size: 2.5rem;
}

.card .controls .play{
    width: 3.2rem;
    height: 3.2rem;
    background:linear-gradient(#fff 0%,#cecaca 100%);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.card .controls .play::before{
    content:'';
    position: absolute;
    height:2.5rem;
    width:2.5rem;
    border-radius:inherit;
    background:linear-gradient(#8dadff, #283885 100%);
}
.card .controls .play i{
    background-color:#fff;
    background-clip:text;
    -webkit-text-fill-color:transparent;
    position:absolute;
}
.card .controls .volume{
    width: 100%;
    height:50px;
    background: #333;
    position: absolute;
    left:0;
    bottom:-10%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition:bottom .3s linear;
    border-radius:10px;
    border:1px solid #555;
    padding:25px;
    z-index:5;
    gap:5px;
}
.card .controls .volume.show{
    bottom:0;
}
.card .controls .volume input{
    width:100%;
    height: 4px;
}