@import url('https://fonts.googleapis.com/css2?family=Poppins&family=Roboto:wght@400;500&display=swap');

*{
  margin: 0;
  padding:0;
  box-sizing: border-box;
  font-family: 'Poppins',sans-serif;
}
body{
  width: 100%;
  min-height: 100vh;
  color: #fff;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image:url('./assets/Image/bgWeb.jpg');
  background-repeat:no-repeat;
  background-size:cover;
  background-position:center;
}
i{
  cursor: pointer;
  user-select: none;
}
ul{
  list-style: none;
}
.container{
  width: 360px;
  border-radius:15px;
  position: relative;
}
.shape{
  width: 150px;
  height: 150px;
  position: absolute;
  border-radius: 50%;
}
.shape-1{
  background:radial-gradient(circle at 35px 65px,#9b22ea,#005);
  top:-65px;
  left:-65px;
}
.shape-2{
  background:radial-gradient(circle at 45px 90px,#ea22b1,#111);
  bottom:-65px;
  right:-65px;
}
.shape-3{
  width: 60px;
  height:60px;
  background:#08e2ff;
  right:7px;
  top: 10px;
  filter:blur(22px);
}
main{
  width: 360px;
  border-radius:15px;
  border:1px solid rgba(255,255,255,.2);
  box-shadow:0 0 20px rgba(7,15,15,.5);
  overflow: hidden;
  position:relative;
  backdrop-filter:blur(3px);
}
